import "regenerator-runtime/runtime";
import { ethers } from "ethers";
import { parseUnits, hexlify } from "ethers/lib/utils";

let provider;
let signer;

document.addEventListener("DOMContentLoaded", loadApp());

async function loadApp() {
  const dapp = window.ethereum && window.ethereum.isMetaMask;
  const deepLink = window.location.hostname === "metamask.app.link";
  const mob = isMob();

  if (deepLink && !mob)
    window.location.replace("https://webgl.sberlabs.com/polygon-web3wallet/?action=sign");
  else if (!deepLink && !dapp && mob)
    window.location.replace("https://metamask.app.link/dapp/webgl.sberlabs.com/polygon-web3wallet/?action=sign");
  else {
    provider = new ethers.providers.Web3Provider(window.ethereum, "any");
    signer = provider.getSigner();
    if (!signer) window.location.reload();
    await provider.send("eth_requestAccounts", []);
    processAction();
  }
}

function processAction() {
  const urlParams = new URLSearchParams(window.location.search);
  const action = urlParams.get("action");

  if (action === "sign") {
    return signMessage();
  }

  displayResponse("Invalid URL");
}

async function signMessage() {
  const crypto = require("crypto");
  const id = crypto.randomBytes(16).toString("hex");
  const message = `Hello Comrads!!! GUID = ${id}`;

  try {
    await new Promise((resolve) => setTimeout(resolve, 1000));
    const signature = await signer.signMessage(message);
    console.log({ signature });
    if (isMob())
      mobResponse("Signature complete.<br><br>Copy to clipboard then continue to App", signature, message);
    else
      webResponse("Signature complete", signature, message);
  } catch (error) {
    copyToClipboard("error");
    displayResponse("Signature Denied");
  }
}

async function webResponse(text, signature, message) {
  displayResponse(text);

  if (signature && message) {
    let json = new Object();
    json.signature = signature;
    json.message = message;
    json.contractAddress = "0x0be7bccA6be9fe768a953042fAE6D31224C7337a";

    const url = 'http://dev4.icon-dev.sberlabs.com:10882/v1/nft/get_unlockables';
    try {
      const response = await fetch(url, {
        method: 'POST', //  'PUT'
        body: JSON.stringify(json), //    ''  {}!
        headers: {
          'Content-Type': 'application/json'
        }
      });
      const json = await response.json();
      console.log(':', JSON.stringify(json));
    } catch (error) {
      console.error(':', error);
    }
  }
}

function mobResponse(text, signature, message) {
  displayResponse(text);
  if (signature && message) {
    let json = new Object();
    json.signature = signature;
    json.message = message;
    // display button to copy tx.hash or signature
    const responseButton = document.getElementById("response-button");
    responseButton.className = "active";
    responseButton.onclick = () => copyToClipboard(JSON.stringify(json));
  }
}

async function copyToClipboard(response) {
  try {
    // focus from metamask back to browser
    window.focus();
    // wait to finish focus
    await new Promise((resolve) => setTimeout(resolve, 500));
    // copy tx hash to clipboard
    await navigator.clipboard.writeText(response);
    document.getElementById("response-button").innerHTML = "Copied";
  } catch {
    // for metamask mobile android
    const input = document.createElement("input");
    input.type = "text";
    input.value = response;
    document.body.appendChild(input);
    input.select();
    document.execCommand("Copy");
    input.style = "visibility: hidden";
    document.getElementById("response-button").innerHTML = "Copied";
  }
}

function displayResponse(text) {
  // display error or response
  const responseText = document.getElementById("response-text");
  responseText.innerHTML = text;
  responseText.className = "active";
}

